import React from "react"
import "./Styles.css"
import { Jumbotron, Navbar, Nav, NavItem, NavLink} from "reactstrap"
import Paper from "@material-ui/core/Paper/Paper";
import Typed from 'react-typed';
import myPhoto from '../resources/myImg.jpg'

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {value: 0};
        this.handleChange = this.handleChange.bind(this);
    }


    handleChange = (event, value) => {
        this.setState({value: value});
        this.props.onTabChange(this.props.navLinks[value]);
    };

    render() {
        const {navLinks} = this.props;
        const {background} = this.props;
        const labels = navLinks.map( (label, i) => {
                return (
                    //<Tab className="text-light" label={label} href={"#" + label}/>
                    <NavItem key={i}>
                        <NavLink className="text-light ml-2 rounded"  href={'#' + navLinks[i]}> {label}</NavLink>
                    </NavItem>
                );
            }
        );

        return (
            <div className={background}>
                <Jumbotron className="bg-transparent mb-0 rounded-0 text-center">
                    <img src={myPhoto} alt="Me" className="rounded-circle border border-light"
                         style={{height: 15 + 'rem', width: 15 + 'rem'}}/>
                    <h1 className="display-3 mainfont">Mazen Amr</h1>

                    <Typed
                        strings={[
                            'Computer Engineer',
                            'Software Engineer',
                            'Hardware Engineer',
                            'CPU Designer',
                            'Android Developer',
                            'Front-End Developer',
                            'Java Developer',
                            'Desktop Developer'
                            ]}
                        typeSpeed={40}
                        backSpeed={50}
                        attr="placeholder"
                        loop >
                        <input type="text" className="lead txtfont bg-transparent border-0 text-center"/>
                    </Typed>
                </Jumbotron>
                <div className={background}>
                    {/*<Paper className="bg-transparent mt-0 rounded-0">*/}
                        {/*<Tabs*/}
                            {/*style={{flexGrow: 1}}*/}
                            {/*value={this.state.value}*/}
                            {/*onChange={this.handleChange}*/}
                            {/*indicatorColor="secondary"*/}
                            {/*centered>*/}
                            {/*{labels}*/}
                        {/*</Tabs>*/}
                    {/*</Paper>*/}

                    <Paper className="bg-transparent">
                        <Navbar color="danger" light expand="md">
                                <Nav className="mx-auto" navbar>
                                    {labels}
                                </Nav>
                        </Navbar>
                    </Paper>
                </div>
            </div>
        );
    }
}


export default Header;